import React, { Component } from "react";
import TextField from "@material-ui/core/es/TextField/TextField";
import Paper from "@material-ui/core/es/Paper";
import MenuList from "@material-ui/core/es/MenuList";
import MenuItem from "@material-ui/core/es/MenuItem";

export default class Autocomplete extends Component {
  state = {
    inputVal: this.props.options[0],
    justUpdated: true
  };

  select(val) {
    this.setState({
      inputVal: val,
      justUpdated: true
    });

    this.props.onChange(val);
  }

  render() {
    const firstItem = React.createRef();
    const options = this.props.options
      // .filter(opt => opt.indexOf(this.state.inputVal) !== -1)
      .filter(opt =>
        new RegExp(".*" + [...this.state.inputVal].join(".{0,3}") + ".*").test(
          opt
        )
      )
      .filter(
        (_e, i, { length }) =>
          (this.state.inputVal.length > 2 || length <= 10) && length >= 1
      );

    return (
      <div
        style={{
          position: "relative",
          maxWidth: "19em",
          marginLeft: "0.6em",
          fontSize: "26px !important",
          zIndex: "99"
        }}
      >
        <TextField
          value={this.state.inputVal}
          margin="normal"
          variant="outlined"
          spellCheck="false"
          fullWidth
          onChange={e =>
            this.setState({
              inputVal: e.target.value.toLowerCase(),
              justUpdated: false
            })
          }
          onKeyUp={e => {
            if (options[0] && e.key === "Enter") {
              this.select(options[0]);
            }
          }}
        />
        <Paper
          style={{
            position: "absolute",
            top: "calc(100% - 8px)",
            left: 0,
            zIndex: 10,
            width: "100%",
            display:
              this.state.justUpdated || options.length === 0 ? "none" : "block"
          }}
        >
          <MenuList>
            {options.map((opt, i) => (
              <MenuItem
                style={{ paddingLeft: "0.3em", paddingRight: "0.3em" }}
                disableGutters
                button
                onClick={() => this.select(opt)}
                key={opt}
                value={opt}
                inputRef={i === 0 && firstItem}
              >
                {opt}
              </MenuItem>
            ))}
          </MenuList>
        </Paper>
      </div>
    );
  }
}
