// @flow
import type { Node } from "react";

import React, { Component } from "react";

type State = {
  text: Node,
  fontSize: number
};

export default class TSpan extends Component<*, State> {
  state = {
    text: null,
    fontSize: 0
  };

  update(state: State) {
    this.setState(state);
  }

  render() {
    return (
      <tspan {...this.props} fontSize={this.state.fontSize}>
        {this.state.text}
      </tspan>
    );
  }
}
