// @flow

import type { Node } from "react";
import React from "react";
import Calculator from "./Calculator";
import TSpan from "./TSpan";

const SizedText = (props: *) => {
  const tspanRef: { current: any } = React.createRef();

  const update = (text: Node, fontSize: number) => {
    tspanRef.current && tspanRef.current.update({ text, fontSize });

    const { onUpdate } = props;
    onUpdate && onUpdate(fontSize);

    console.log(text, fontSize);
  };

  const spanProps = Object.assign({}, props);

  delete spanProps.onUpdate;
  delete spanProps.update;

  return (
    <text>
      <TSpan {...spanProps} ref={tspanRef} />
      <Calculator {...props} update={update}>
        {props.children}
      </Calculator>
    </text>
  );
};

export default SizedText;
