// @flow

import React, { Component } from "react";
import { District } from "../../contexts";

export default class DistrictTemplate extends Component<*> {
  static contextType = District;

  ref: { current: any } = React.createRef();

  getPrint() {
    const { current } = this.ref;
    return current && typeof current.getPrint === "function"
      ? current.getPrint()
      : "";
  }
}
