// @flow

import React, { useEffect } from "react";

const Calculator = (props: *) => {
  const ref: { current: any } = React.createRef();

  useEffect(() => {
    const { width, children, update } = props;
    const span: any = ref.current;
    const fieldWidth = Math.round(span.getBBox().width);

    update(children, width * (width / fieldWidth));
  });

  const spanProps = Object.assign(
    {},
    {
      fill: "none",
      stroke: "none",
      fontSize: props.width,
      ref: ref
    }
  );

  delete spanProps.onUpdate;
  delete spanProps.update;

  return <tspan {...spanProps}>{props.children}</tspan>;
};

export default Calculator;
