// @flow

import React, { Component } from "react";
import SizedText from "../SizedText";
import FuckBrooklyn from "../printParts/FuckBrooklyn";

type Props = {
  text: string,
  style: ?any
};

function determineLinesNumber(text) {
  const { length } = text;

  if (length % 2 !== 0) {
    if (length < 13) {
      return 1;
    }
    return 3;
  } else {
    if (length < 6) {
      return 1;
    }
    if (length < 12) {
      return 2;
    }
    return 3;
  }
}

function getLinesLengths(textLength, linesNumber) {
  const lengths = new Array(linesNumber).fill(0);
  for (let i = 0; i < textLength; i++) {
    lengths[i % linesNumber] += 1;
  }
  return lengths;
}

function splitIntoLines(text, linesNumber) {
  const nameLength = text.length;
  const lines = new Array(linesNumber).fill("");
  const lengths = getLinesLengths(text.length, linesNumber);
  let lineIndex = 0;

  for (let i = 0; i < nameLength; i++) {
    if (lines[lineIndex].length >= lengths[lineIndex]) {
      lineIndex += 1;
    }

    lines[lineIndex] += text[i];
  }

  return lines;
}

const LoafPart = ({ y, isInverted }) => {
  const params = {
    x: 60,
    width: 171,
    fill: "#ffdb4d"
  };

  return (
    <>
      {isInverted ? (
        <>
          <rect y={y} height={8} {...params} />
          <rect y={y + 10} height={5} {...params} />
          <rect y={y + 17} height={3} {...params} />
        </>
      ) : (
        <>
          <rect y={y} height={3} {...params} />
          <rect y={y + 5} height={5} {...params} />
          <rect y={y + 12} height={8} {...params} />
        </>
      )}
    </>
  );
};

class Loaf extends Component<*, *> {
  state = {
    height: 0
  };

  updateHeight(height) {
    this.setState({ height });
  }

  render() {
    const { y } = this.props;
    return (
      <>
        <LoafPart y={y} isInverted={false} />
        <LoafPart y={y + this.state.height} isInverted={true} />
      </>
    );
  }
}

class Msk extends Component<*, *> {
  // text: { current: any } = React.createRef();
  state = { y: 0 };

  setY(y) {
    this.setState({ y });
    // this.text.current &&
    //   this.text.current.setAttribute("transform-origin", `150 ${y}`);
  }

  render() {
    return (
      <>
        <g transform={`translate(150 ${this.state.y})`}>
          <text
            x={0}
            y={0}
            fill="#ffdb4d"
            fontFamily="rubik"
            fontSize={32}
            transform="skewY(200) scale(0.8 1)"
            // ref={this.text}
            alignmentBaseline="hanging"
          >
            MSK
          </text>
        </g>
        {/*<circle cx={150} cy={this.state.y} fill="#f00" r={3} />*/}
      </>
    );
  }
}

const updateRef = (ref, f) => {
  ref.current && f(ref.current);
};

export default class extends React.Component<Props, *> {
  svgRef: { current: any } = React.createRef();

  getPrint() {
    const { current } = this.svgRef;
    return current ? current.outerHTML : "";
  }

  render() {
    const { text, style } = this.props;
    // не мог сделать более одной ссылки на компонет
    const loaf: { current: any } = React.createRef();
    const msk: { current: any } = React.createRef();
    const linesCount = determineLinesNumber(text);

    return (
      <svg
        viewBox="0 0 297 420"
        style={style}
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        ref={this.svgRef}
      >
        <g transform="skewY(-30) scale(0.9 1) translate(17 0)">
          <Loaf y={168} ref={loaf} />
          <SizedText
            x={60}
            y={180}
            width={177}
            strokeWidth={2}
            fill="#ffdb4d"
            onUpdate={h => {
              updateRef(loaf, curr => curr.updateHeight(h * linesCount * 0.86));

              updateRef(msk, curr => curr.setY(190 + h * linesCount * 0.6));
            }}
          >
            {splitIntoLines(text, linesCount).map((t, i) => (
              <tspan
                x={60}
                dy="0.95em"
                key={i}
                lengthAdjust="spacing"
                fontFamily="BebasNue"
                fontWeight="bold"
              >
                {t.toUpperCase()}
              </tspan>
            ))}
          </SizedText>
        </g>
        <Msk ref={msk} />

        <FuckBrooklyn
          transform="skewY(196) scale(0.7) translate(82, 0)"
          fill="#ffdb4d"
        />
      </svg>
    );
  }
}
