// @flow

import React from "react";
import Print from "../prints/Doshirak";
import DistrictTemplate from "./DistrictTemplate";

export default class JMother extends DistrictTemplate {
  render() {
    const { district, color } = this.context;

    return (
      <Print
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          display: "block",
          width: "100%",
          height: "100%"
        }}
        text={district}
        ref={this.ref}
        color={color}
      />
    );
  }
}
