// @flow

import React from "react";
import Template from "./Template";
import District from "./District";

// это менять только в крайнем случае
const CANVAS_WIDTH = 297;

const BIG_FZ = 50;
const SMALL_FZ = 35;
const OFFSET_RIGHT = 50;
const BASE_TEMPLATE_OFFSET = 32;
const WORDS_TO_REPLACE = [
  { value: "бульвар", size: 8.8 },
  { value: "улица", size: 11.8 },
  { value: "проспект", size: 8 },
  { value: "площадь", size: 7.8 },
  { value: "шоссе", size: 11.8 },
  { value: "восточное", size: 6.9 },
  { value: "западное", size: 7.6 },
  { value: "северное", size: 7.8 },
  { value: "южное", size: 10 },
  { value: "верхние", size: 9 },
  { value: "нижние", size: 9 },
  { value: "центральное", size: 5.7 }
].map(({ value, size }) => ({
  value: value.toUpperCase(),
  size
}));

export default class Doshirak extends React.Component<*> {
  svgRef: { current: any } = React.createRef();

  getPrint() {
    const { current } = this.svgRef;
    return current ? current.outerHTML : "";
  }

  render() {
    const { style, text: textInput, color: mainColor } = this.props;

    let text = textInput.toUpperCase(),
      label;

    for (let i = 0; i < WORDS_TO_REPLACE.length; i++) {
      let word = WORDS_TO_REPLACE[i];

      if (!label && text.includes(word.value)) {
        label = word;
        text = text.replace(word.value, "").trim();
      }
    }

    const twoColumns = text.length >= 7;
    const fz = twoColumns ? SMALL_FZ : BIG_FZ;
    const textWidth = twoColumns ? fz * 1.92 : fz * 0.91;
    const xTextStart = CANVAS_WIDTH - OFFSET_RIGHT;
    const xTextEnd = xTextStart - textWidth;

    return (
      <svg
        viewBox={`0 0 ${CANVAS_WIDTH} 420`}
        style={style}
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        ref={this.svgRef}
      >
        <Template
          color={mainColor}
          transform={`translate(${xTextEnd - BASE_TEMPLATE_OFFSET} 0)`}
        />

        <path
          strokeWidth={2}
          stroke={mainColor}
          d={`M ${xTextStart} 0 H ${xTextEnd}`}
        />

        <District
          fz={fz}
          value={text}
          twoColumns={twoColumns}
          x={xTextStart - textWidth}
          y={label ? label.size + 5 : 0}
          color={mainColor}
        />

        {label && (
          <text
            fill={mainColor}
            y={5}
            x={xTextEnd}
            width={textWidth}
            alignmentBaseline="hanging"
            dominantBaseline="text-before-edge"
            fontFamily="Montserrat"
            fontStyle="italic"
            fontWeight="900"
            fontSize={label.size * (twoColumns ? (2 * SMALL_FZ) / BIG_FZ : 1)}
          >
            {label.value}
          </text>
        )}
      </svg>
    );
  }
}
